import React from 'react';
import '../../assets/css/style.css';
import Logo from '../../assets/images/logo.png';
import Footer from '../../components/Footer';

function PrivacyPolicy() {
  return (
    <div id='page-name' className='body banner'>
      <div className='page-container'>
        <a href='/#'>
          <img width='90' src={Logo} alt='pluzo' />
        </a>

        <h2 className='heading-color'>Privacy Policy</h2>
        <span className='text-color'>
          <p>
            This privacy policy will explain what information we collect online, why we collect it, and the choices we offer to you.
            This privacy policy (“Policy”) describes the data practices of Pluzo LLC
            (“Company”), covering its services under its control, including the
            mobile application known as Pluzoany and allthe Company website, and
            any other website or mobile application (collectively "Services").
            By using the Services, you are consenting to this Policy and the
            processing of your data in the manner provided in this Policy. If
            you do not agree with these terms, please do not use the Services.
            This Privacy Policy was last updated on <b>January 19, 2021</b>.
          </p>
          <h3>
            <b>1. Information we collect</b>
          </h3>
          <p className='first-child'>
            We may collect information about the Services you use and how you use them, such as data regarding your usage of the App and activity in the App. We collect PII, DII, and log information about your interactions as described below.
            Personally identifiable information (PII) is information that can be used to identify or contact you online or offline, such as your name, address, email, phone number, photos or audio data, and payment information, or data that is linked to such identifiers. The Services may collect PII when it is provided to us, such as when you use our Services, attempt to contact us, submit a resume or job application, or connect with us on social media or one of our partners.
            We may also create or collect device-identifiable information (DII), such as cookies, unique device and advertising identifiers, statistical identifiers, usernames, and similar identifiers that are linkable to a browser or device. From these platforms, we may also receive other information, such as your IP address, user agent, timestamps, precise and imprecise geolocation, sensor data, apps, fonts, battery life information, and screen size.
            Our Services also collect information about your interactions, including navigation paths, search queries, crashes, timestamps, purchases, clicks and shares, and referral URLs. We may combine this data with PII and DII. For efficiency’s sake, information about your interactions may be transmitted to our servers while you are not using the Services. We may also partner with third-parties that collect additional information – please see their privacy policies for more details and see below for your choices regarding these parties.

          </p>
          <h3>
            <b>2. Categories of PII We Collect</b>
          </h3>
          <p className='first-child'>
            We have collected the following information from consumers within
            the past 12 months.
            <br />
            <table>
              <thead>
                <tr>
                  <th>Category Number</th>
                  <th>Category</th>
                  <th>Examples </th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <th>1</th>
                  <th>Identifiers</th>
                  <th>
                    Name, email address, IP address, online identifier, images the user uploads, email address, device identifiers, username, or other similar data
                  </th>
                </tr>
                <tr>
                  <th>2</th>
                  <th>Internet or electronic network activity information</th>
                  <th>
                    Information regarding a consumer’s interaction with an Internet website, application, or advertisement, in-app activity including, for example, time a user spends on a particular webpage, number of swipes, number of connections, number of chats a user sends, number of friends each user has, and clicks.
                  </th>
                </tr>
                <tr>
                  <th>3</th>
                  <th>Geolocation</th>
                  <th>Precise geographic location</th>
                </tr>
                <tr>
                  <th>4</th>
                  <th>
                    Audio, electronic, visual, or similar information
                  </th>
                  <th>
                    Pictures of users that they submit, audio of the user interaction via the livestream.
                  </th>
                </tr>
                <tr>
                  <th>5</th>
                  <th>
                    Commercial Information
                  </th>
                  <th>
                    Records of services purchased
                  </th>
                </tr>
                <tr>
                  <th>6</th>
                  <th>
                    Inferences drawn from any information described herein to create a profile about a consumer
                  </th>
                  <th>
                    Profile reflects consumer reflecting the consumer’s preferences, characteristics, psychological trends, preferences, predispositions, behavior, attitudes, intelligence, abilities, and aptitudes.
                  </th>
                </tr>
              </tbody>
            </table>
          </p>

          <h3>
            <b>3. How we use information we collect</b>
          </h3>
          <p className='first-child'>
            We use the information we collect from our Services to provide, maintain, protect and improve our Services, to develop new Services and offerings, and to protect us and our users.
            <br />
            PII is primarily used for business purposes, such as responding to inquiries and providing the Services. When you contact us, we may keep a record of your communication as well as the other information to help solve any issues you might be facing. We may use your email address to inform you about our Services, such as letting you know about changes or improvements. We also use your PII for the following business purposes:
            <p className='first-child'>
              A. To meet the reason for which the PII was provided.
              <br />
              B. To provide you with information or services you request from
              us.
              <br />
              C. To improve our website and performance of the contents therein.
              <br />
              D. To resolve disputes.
              <br />
              E. Preventing potentially fraudulent, prohibited, or illegal
              activities.
              <br />
              F. Enforcing the Terms of Service.
              <br />
              G. Performing other duties as required by law.
            </p>
            We share PII and DII with companies, outside organizations, and
            individuals for limited reasons and the following business purposes,
            outlined below:
            <p className='first-child'>
              A.	We will share PII and other data with companies, outside organizations or individuals if we have your consent to do so.
              <br />
              B.	For external processing - We provide PII to our affiliates or other trusted businesses or persons to process it for us, based on our instructions and in compliance with our Privacy Policy and any other appropriate confidentiality and security measures.
              <br />
              C.	For legal reasons - We will share PII with companies, outside organizations or individuals if we have a good-faith belief that access, use, preservation or disclosure of the information is reasonably necessary to meet any applicable law, regulation, legal process or enforceable governmental request, detect, prevent, or otherwise address fraud, security or technical issues or protect against harm to the rights, property or safety of our users or the public as required or permitted by law.
              <br />
              D.	In case of a sale or asset transfer - If we become involved in a merger, acquisition or other transaction involving the sale of some or all of our assets, user information, including PII collected from you through your use of our Services, could be included in the transferred assets. Should such an event occur, we will use reasonable means to notify you, either through email and/or a prominent notice on the Services.
              <br />
              E.	We may share aggregated information and DII with our partners such as businesses we have a relationship with, advertisers or connected sites. For example, we may share information to show trends about the general use of our services.
              <br />
              F.	With third-party analytics and ad-tech companies for the purpose of providing analytics to improve our Services and providing and delivering targeted advertising.
            </p>
            We disclose your PII for business purposes to the following
            categories of third-parties:
            <p className='first-child'>
              A. Our affiliates
              <br />
              B. Services providers
              <br />
              C. APIs
              <br />
              D. Third-parties
            </p>
            We use DII to operate our Services and manage user sessions, including analyzing usage of our Services, preventing malicious behavioral and fraud, improving the content, to link your identity across devices and browsers in order to provide you with a more seamless experience online, and helping third-parties provide relevant advertising and related metrics. We share DII with third-parties primarily for advertising and analytics purposes, for external processing, and for security purposes.
          </p>
          <h3>
            <b>4. Third-Parties</b>
          </h3>
          <p className='first-child'>
            While we strive to work with reputable companies with good privacy practices, this Policy does not apply to services offered by other companies or individuals, including products or sites that may be displayed to you on the Services. We also do not control the privacy policies and your privacy settings on third-party sites, including social networks. We may use third-parties to help offer you more tailored ads and better Services, such as obtaining analytics about the users of our site and to help tailor advertising to your preferences. For further information, please see the relevant privacy policies for each third-party and industry codes of conduct including, <br></br>
            <a href='https://unity3d.com/legal/privacy-policy'>
              https://unity3d.com/legal/privacy-policy
            </a>
          </p>
          <h3>
            <b>5. Choice</b>
          </h3>
          <p className='first-child'>
            Like many other companies, we do not honor DNT flags but instead offer other choices with respect to third-parties. Many third-parties participate in self-regulation to offer you a choice regarding receiving targeted ads. Please note that you’ll still see generic ads after opting out, but they won’t be based on your activities online. On the web, you can opt out of participating companies by visiting the following sites:<br></br>
            <a href='http://optout.networkadvertising.org/'>
              • http://optout.networkadvertising.org/
            </a>
            <br></br>
            <a href='http://optout.aboutads.info/'>
              • http://optout.aboutads.info/
            </a>
            <br></br>
            <a href='http://youronlinechoices.eu/'>
              • http://youronlinechoices.eu/
            </a>
            <br></br>
            <a href='https://tools.google.com/dlpage/gaoptout'>
              • https://tools.google.com/dlpage/gaoptout
            </a>
            <br></br>
            <a href='https://support.apple.com/en-us/HT202074'>
              • https://support.apple.com/en-us/HT202074
            </a>
            <br />
            If you see a Unity ad, you can opt-out then or at any time by
            clicking or tapping the “ℹ” button (or Data Privacy icon) on any ad
            you see (the “ℹ” button or Data Privacy icon will also allow you to
            access the data that is collected about you in that particular app)
            <br />
            If you wish to similarly opt out of cross-app advertising on mobile
            devices, you can enable the Limit Ad Tracking flag on the device.
            Enabling Limit Ad Tracking sends a flag to third-parties that you
            wish to opt out of targeted advertising on that device, and major
            mobile platforms require companies to honor this flag. Screenshots
            on how to find these options on various devices are available here:
            <a href='http://www.networkadvertising.org/mobile-choices'>
              http://www.networkadvertising.org/mobile-choices
            </a>
            . To learn how to opt out on other devices, please visit the
            platform’s privacy policies for more information.
          </p>
          <h3>
            <b>6. Information security</b>
          </h3>
          <p className='first-child'>
            We work hard to protect our users from unauthorized access to or unauthorized alteration, disclosure or destruction of information we hold and undertake reasonable security measures with appropriate confidentiality, integrity, and availability protections. However, since no software or storage system is 100% secure, we cannot guarantee for the security of your information associated with the Services, or any other service for that matter. You can help protect your account information by using unique and hard-to-guess passwords. We retain data for the maximum amount of time allowable under applicable law.
          </p>
          <h3>
            <b>7. Children Under 13</b>
          </h3>
          <p className='first-child'>
            We do not knowingly collect information for any child under the age of 13. If you are the parent of a child under the age of 13 and have a concern regarding your child’s information on our Services, please contact us at the email provided at the end of this Policy.
          </p>
          <h3>
            <b>8. Changes</b>
          </h3>
          <p className='first-child'>
            Our Privacy Policy may change from time to time. We will post any Policy changes on this page and within the settings of any mobile apps. Please check back periodically to view changes to our privacy policy.
          </p>
          <h3>
            <b>9. Questions?</b>
          </h3>
          <p className='first-child'>
            If you have questions or requests regarding our privacy practices,
            please contact us at contact@pluzo.com.
          </p>
        </span>
      </div>
      <Footer />
    </div>
  );
}

export default PrivacyPolicy;
