import React from 'react';
// import images
import Logo from '../assets/images/logo.png';
import AppStoreIcon from '../assets/images/footer-icon-app.svg';
import GooglePlayIcon from '../assets/images/footer-icon-google.svg';
import { Link } from 'react-router-dom';

function Footer() {
  return (
    <div id='footer-wrap'>
      <div className='container'>
        <footer id='footer'>
          <div className='row align-items-center'>
            <div className='col-md-8'>
              <div className='footer-section-wrap'>
                <div className='footer-logo'>
                  <a href='/#'>
                    <img src={Logo} alt='pluzo' />
                  </a>
                </div>
                <div className='footer-nav'>
                  <ul>
                    <Link to='/tos'>Terms of Service</Link>
                    <div>&nbsp;&nbsp;&nbsp;</div>
                    <Link to='/policy'>Privacy Policy</Link>

                    {/* <li><a href="/#">Term of Service</a></li>
                                        <li><a href="/#">Privacy Policy</a></li> */}
                  </ul>
                </div>
              </div>
            </div>
            {/* <div className="col-md-4">
                            <div className="footer-icons">
                                <ul>
                                    <li><a href="/#"><img src={AppStoreIcon} alt="" /></a></li>
                                    <li><a href="/#"><img src={GooglePlayIcon} alt="" /></a></li>
                                </ul>
                            </div>
                        </div> */}
          </div>
        </footer>
      </div>
    </div>
  );
}

export default Footer;
